.pagination {
  display: flex;
  margin: 40px;
  justify-content: center;
  margin-top: 100px;
}
@media (max-width: 415px) {
  .pagination {
    margin-top: 50px;
  }
  .ant-pagination {
    font-size: 13px;
  }
  .form {
    margin-top: 25px;
  }
  .search {
    width: 70%;
    height: 25px;
    border: 1px solid #016d1a;
    border-radius: 10px;
  }
  .quest {
    width: 60px;
    height: 25px;
    border: 1px solid #016d1a;
    border-radius: 10px;
  }
  .lose {
    width: 60px;
    height: 25px;
    border: 1px solid #016d1a;
    border-radius: 10px;
  }
}

.ant-pagination {
  background-color: #2a9932;
  padding: 5px;
}

.ant-pagination .ant-pagination-item a {
  color: #fff;
}
.ant-pagination .ant-pagination-item a:hover {
  color: black;
}
.ant-pagination .ant-pagination-item-active {
  background-color: rgb(39, 90, 29);
  border-color: black;
}
.ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
  background-color: rgb(39, 90, 29);
  border-color: black;
}
.loading {
  color: black;
}
.form {
  margin-top: 10px;
}

.search {
  width: 30%;
  height: 30px;
  color: black;
  background: #c2fdc5;
  border: 2px solid #016d1a;
  border-radius: 50px;
}

.quest {
  width: 70px;
  height: 30px;
  color: #00360d;
  background: #aef8b2;
  border: 2px solid #016d1a;
  border-radius: 50px;
}

.lose {
  width: 70px;
  height: 30px;
  color: #00360d;
  background: #aef8b2;
  border: 2px solid #016d1a;
  border-radius: 50px;
}
