.cardcompetitions {
  width: 300px;
  height: 150px;
  color: #b0f799;
  border-radius: 10px;
  text-align: center;
  border: 3px solid #b0f799;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 50px;
  background: #2a9932;
}
@media (max-width: 1367px) {
  .cardcompetitions {
    width: 260px;
    height: 140px;
  }
  .cards {
    font-size: 23px;
  }
}
@media (max-width: 415px) {
  .cardcompetitions {
    width: 220px;
    height: 120px;
  }
  .cards {
    margin-bottom: 5px;
    font-size: 20px;
  }
}
.cardcompetitions:hover {
  background-color: #b0f799;
}
.cards {
  font-size: 30px;
}

.CardCompetitionsButton {
  text-decoration: none;
}
